

import { __private, Asset, assetManager, AssetManager, resources } from "cc";
import { utils } from "../utils/utils";
import { Fwk } from "../../Fwks";

export type AssetType<T = Asset> = __private.__types_globals__Constructor<T>;

/**
 * 资源操作选项
 */
export interface ResOptions<T extends Asset = Asset> {
    /** 资源路径 */
    path: string;
    /** 资源类型（加载时必传，释放时可选） */
    type?: AssetType<T>;
    /** 资源包名（可选，默认使用 defaultBundleName） */
    bundleName?: string;
}

/**
 * 资源管理器
 */
export class ResManager {


    /** 全局默认加载的资源包名 */
    defaultBundleName: string = "resources";

    /**
     * 加载资源
     * @param options 加载选项
     */
    async load<T extends Asset>(options: ResOptions<T> & { type: AssetType<T> }): Promise<T> {
        const bundleName: string = options.bundleName || this.defaultBundleName;
        const bundle: AssetManager.Bundle = await this.ensureBundle(bundleName);
        const path: string = options.path;
        const type: AssetType<T> = options.type;
        
        const assetPromise: Promise<T> = new Promise<T>((resolve, reject) => {
            bundle.load(path, type, (err: Error | null, asset: T) => {
                if (err) {
                    reject(err);
                    Fwk.log.error(`ResManager: 资源加载失败 - Bundle: ${bundleName}, Path: ${path}, Error: ${err.message}`);
                } else {
                    resolve(asset);
                }
            });
        });

        return assetPromise;
    }

    /**
     * 加载资源包
     * @param name 资源包名
     * @param options 资源参数,例:{ version: "74fbe" }
     */
    async loadBundle(name: string, options: Record<string, any> = {}): Promise<AssetManager.Bundle> {
        const bundlePromise: Promise<AssetManager.Bundle> = new Promise<AssetManager.Bundle>((resolve, reject) => {
            assetManager.loadBundle(name, options, (err: Error | null, bundle: AssetManager.Bundle) => {
                if (err) {
                    reject(err);
                    Fwk.log.error(`ResManager: 资源包加载失败 - Name: ${name}, Error: ${err.message}`);
                } else {
                    resolve(bundle);
                }
            });
        });

        return bundlePromise;
    }

    /**
     * 获取资源包
     * @param name 资源包名
     */
    getBundle(name: string): AssetManager.Bundle | undefined {
        return assetManager.bundles.get(name);
    }

    /**
     * 确保资源包已加载
     */
    private async ensureBundle(name: string): Promise<AssetManager.Bundle> {
        let bundle = assetManager.bundles.get(name);
        if (!bundle) {
            bundle = await this.loadBundle(name);
        }
        return bundle;
    }


    /**
     * 释放资源
     * @param options 释放选项
     */
    release(options: ResOptions) {
        const bundleName = options.bundleName || this.defaultBundleName;
        const bundle = assetManager.getBundle(bundleName);

        if (bundle) {
            const asset = bundle.get(options.path);
            if (asset) {
                asset.decRef();
            }
        }
    }


}